import React from 'react'
import Background1 from '../Images/Group 2.png'
import Navbar from '../components/Navbar'
import About from './About'
import VisiMisi from './VisiMisi'
import ServiceandExp from './ServiceandExp'
import Penawaran from './Penawaran'
import Contacs from './Contacs'
import Footer from './Footer'
function Home() {
  return (
    <div className="bg-white">
      <Navbar />
      <div className="relative">
        <img src={Background1} alt="" className="phone:h-[18vh] tablet:h-[20vh] tablet:w-full laptop:h-full laptop:w-full  desktop:h-full" />
        <div className="absolute desktop3:top-[35%] phone:top-[30%]  tablet:top-[30%] laptop:top-[30%] desktop:top-[30%] desktop2:top-[30%] left-[11%]">
          <p className="text-white font-Roboto text-left font-bold text-[56px] phone:text-[20px] tablet:text-[20px] laptop:text-[30px] desktop:text-[40px] desktop2:text-[40px]">
            Lorem ipsum dolor <br /> sit amet consectetur adipisicing elit.
          </p>
        </div>
      </div>
      <About />
      <VisiMisi />
      <ServiceandExp />
      <Penawaran />
      <Contacs />
      <Footer />
    </div>
  )
}

export default Home
