import React, { Component } from 'react'
import Slider from 'react-slick'

export default class AutoPlayMethods extends Component {
  constructor(props) {
    super(props)
    this.play = this.play.bind(this)
    this.pause = this.pause.bind(this)
  }
  play() {
    this.slider.slickPlay()
  }
  pause() {
    this.slider.slickPause()
  }
  render() {
    const settings = {
      dots: true,
      infinite: true,
      fade: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay: true,
      autoplaySpeed: 10000,
    }
    return (
      <div className="desktop3:px-[5%] phone:px-[9%] phone:pb-[10%] tablet:px-[6%] tablet:pb-[10%] laptop:px-[8%] laptop:pb-[5%] desktop:px-[5%] desktop:pb-[5%] desktop2:px-[5%] desktop2:pt-[2%] desktop2:pb-[4%]">
        <Slider ref={(slider) => (this.slider = slider)} {...settings}>
          <div className="desktop3:px-[10%] phone:px-[1%] tablet:px-[1%]  laptop:px-[1%]">
            <img src={this.props.image1} className="" alt="" />
          </div>
          <div className="desktop3:px-[10%] phone:px-[1%] tablet:px-[1%]  laptop:px-[1%]">
            <img src={this.props.image1} className="" alt="" />
          </div>
          <div className="desktop3:px-[10%] phone:px-[1%] tablet:px-[1%]  laptop:px-[1%]">
            <img src={this.props.image1} className="" alt="" />
          </div>
        </Slider>
      </div>
    )
  }
}
