import React from 'react'
import Navbar from '../components/Navbar'
import Contacs from './Contacs'
import { Tree, TreeNode } from 'react-organizational-chart'
import Footer from './Footer'

function Struktur() {
  return (
    <div>
      <Navbar />
      <div className="desktop3:px-[11%] desktop:px-[7%] desktop2:px-[7%] phone:px-[5%] tablet:px-[5%] laptop:px-[8%] py-[1.5%] mt-[3%] ">
        <div className="flex items-center mb-[2rem]">
          <div className="bg-[#EAB200] mr-4 w-[0.2vw] h-[4vh] phone:w-[1vw] tablet:w-[1vw] laptop:w-[0.4vw] desktop:w-[0.4vw] desktop2:w-[0.4vw]"></div>
          <p className="text-[38px] desktop:text-[32px] desktop2:text-[32px] phone:text-[24px] tablet:text-[24px] laptop:text-[24px] font-Quicksand font-bold text-[#031E33]">Struktur Organisasi</p>
        </div>
        <div className="my-[1%] text-white text-[20px] phone:text-[16px] tablet:text-[16px] laptop:text-[16px] desktop:text-[16px] desktop2:text-[16px] bg-[#031E33] px-4 py-[3%] border rounded">
          <Tree lineWidth={'3px'} lineHeight={'100px'} lineColor={'#EAB200'} lineBorderRadius={'10px'} style={{ fontSize: '40px' }} label="Commissioner">
            <TreeNode label="Director">
              <TreeNode label="Project Manager">
                <TreeNode label="Site Supervisor" />
              </TreeNode>
              <TreeNode label="Administration & Marketing" />
            </TreeNode>
          </Tree>
        </div>
      </div>

      <Contacs />
      <Footer />
    </div>
  )
}

export default Struktur
