import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import Logo from '../Images/Logo.png'
import Hamburger from '../Images/hamburger.png'
import Close from '../Images/close.png'
function Navbar() {
  const location = window.location.pathname
  const navigate = useNavigate()
  const [navbar, setNavbar] = useState(false)
  return (
    <div className="phone:relative tablet:relative laptop:relative">
      <div className="px-[10%] phone:px-[7%] tablet:px-[7%] laptop:px-[7%] desktop:px-[3%] desktop2:px-[3%] py-[1.3%] bg-white shadow">
        <div className="flex items-center justify-between  phone:py-[10px] tablet:py-[10px] laptop:py-[10px]">
          <img src={Logo} alt="" className="w-[3vw] phone:w-[12vw] tablet:w-[10vw] desktop:w-[5vw] desktop2:w-[5vw] laptop:w-[6vw]" />
          <div className="flex items-center gap-x-8 desktop:gap-x-6 phone:hidden tablet:hidden laptop:hidden ">
            {location === '/' ? (
              <a href="#about" className="cursor-pointer desktop:text-[14px] hover:font-bold">
                <p>Tentang Kami</p>
              </a>
            ) : (
              <p onClick={() => navigate('/')} className="cursor-pointer desktop:text-[14px]  hover:font-bold">
                Tentang Kami
              </p>
            )}
            {location === '/' ? (
              <a href="#servis" className="cursor-pointer desktop:text-[14px]  hover:font-bold">
                <p>Bidang Usaha</p>
              </a>
            ) : (
              <p onClick={() => navigate('/')} className="cursor-pointer desktop:text-[14px]  hover:font-bold">
                Bidang Usaha
              </p>
            )}

            {location === '/' ? (
              <a href="#penawaran" className="cursor-pointer desktop:text-[14px]  hover:font-bold">
                <p>Penawaran</p>
              </a>
            ) : (
              <p onClick={() => navigate('/')} className="cursor-pointer desktop:text-[14px]  hover:font-bold">
                Penawaran
              </p>
            )}
            <p onClick={() => navigate('/pengalaman-kami')} className={`cursor-pointer desktop:text-[14px]   ${location === '/pengalaman-kami' ? 'font-bold' : 'hover:font-bold'}`}>
              Pengalaman Kami
            </p>
            <p onClick={() => navigate('/struktur-organisasi')} className={`cursor-pointer desktop:text-[14px]   ${location === '/struktur-organisasi' ? 'font-bold' : 'hover:font-bold'}`}>
              Struktur Organisasi
            </p>
            <p onClick={() => navigate('/portofolio')} className={`cursor-pointer desktop:text-[14px]   ${location === '/portofolio' ? 'font-bold' : 'hover:font-bold'}`}>
              Portofolio
            </p>

            {location === '/' ? (
              <a href="#contacs" className="cursor-pointer desktop:text-[14px]  hover:font-bold">
                <p>Hubungi Kami</p>
              </a>
            ) : (
              <p onClick={() => navigate('/')} className="cursor-pointer desktop:text-[14px]  hover:font-bold">
                Hubungi Kami
              </p>
            )}
          </div>
          {navbar === false ? (
            <img src={Hamburger} onClick={() => setNavbar(!navbar)} className="phone:w-[6vw] tablet:w-[4vw]  laptop:w-[3vw]  desktop3:hidden desktop2:hidden desktop:hidden" alt="" />
          ) : (
            <img src={Close} onClick={() => setNavbar(!navbar)} className="phone:w-[6vw] tablet:w-[4vw]  laptop:w-[3vw]  desktop3:hidden desktop2:hidden desktop:hidden" alt="" />
          )}
        </div>
      </div>
      <div className="absolute phone:top-[80px] tablet:top-[95px] laptop:top-[90px] w-full h-full z-[99] desktop3:hidden desktop2:hidden desktop:hidden">
        {navbar !== false && (
          <div className="bg-[#031E33] text-white px-[5%] py-[3%]">
            {location === '/' ? (
              <a href="#about" className="cursor-pointer hover:font-bold ">
                <p className="pb-[10px]">Tentang Kami</p>
              </a>
            ) : (
              <p onClick={() => navigate('/')} className="cursor-pointer hover:font-bold pb-[10px]">
                Tentang Kami
              </p>
            )}
            {location === '/' ? (
              <a href="#servis" className="cursor-pointer hover:font-bold ">
                <p className="pb-[10px]">Bidang Usaha</p>
              </a>
            ) : (
              <p onClick={() => navigate('/')} className="cursor-pointer hover:font-bold pb-[10px]">
                Bidang Usaha
              </p>
            )}

            {location === '/' ? (
              <a href="#penawaran" className="cursor-pointer hover:font-bold">
                <p className="pb-[10px]">Penawaran</p>
              </a>
            ) : (
              <p onClick={() => navigate('/')} className="cursor-pointer hover:font-bold pb-[10px]">
                Penawaran
              </p>
            )}
            <p onClick={() => navigate('/pengalaman-kami')} className={`cursor-pointer  ${location === '/pengalaman-kami' ? 'font-bold' : 'hover:font-bold'} pb-[10px]`}>
              Pengalaman Kami
            </p>
            <p onClick={() => navigate('/struktur-organisasi')} className={`cursor-pointer  ${location === '/struktur-organisasi' ? 'font-bold' : 'hover:font-bold'} pb-[10px]`}>
              Struktur Organisasi
            </p>
            <p onClick={() => navigate('/portofolio')} className={`cursor-pointer  ${location === '/portofolio' ? 'font-bold' : 'hover:font-bold'} pb-[10px]`}>
              Portofolio
            </p>

            {location === '/' ? (
              <a href="#contacs" className="cursor-pointer hover:font-bold">
                <p>Hubungi Kami</p>
              </a>
            ) : (
              <p onClick={() => navigate('/')} className="cursor-pointer hover:font-bold ">
                Hubungi Kami
              </p>
            )}
          </div>
        )}
      </div>
    </div>
  )
}

export default Navbar
