import React, { useState } from 'react'
import Rumah from '../Images/rumah.jpg'
import Rumah1 from '../Images/IMG-8170.JPG'
import Modal from 'react-modal'
import SlideImg from './SlideImage'
import Fade from 'react-reveal/Fade'
function Portofolio() {
  const [modalIsOpen, setIsOpen] = useState(false)
  function openModal() {
    setIsOpen(true)
  }
  function closeModal() {
    setIsOpen(false)
  }

  return (
    <Fade>
      <div id="portofolio" className="px-[0%]  mt-[3%]">
        <div className="grid desktop3:grid-cols-4 laptop:grid-cols-2 desktop:grid-cols-2 desktop2:grid-cols-3 gap-5">
          <img src={Rumah1} onClick={openModal} className="h-full rounded cursor-pointer" alt="" />
          <img src={Rumah} className="h-full rounded" alt="" />
          <img src={Rumah} className="h-full rounded" alt="" />
          <img src={Rumah} className="h-full rounded" alt="" />
          <img src={Rumah} className="h-full rounded" alt="" />
          <img src={Rumah} className="h-full rounded" alt="" />
          <img src={Rumah} className="h-full rounded" alt="" />
          <img src={Rumah} className="h-full rounded" alt="" />
        </div>
        <Modal isOpen={modalIsOpen} className="edit2" contentLabel="Example Modal" overlayClassName="Overlay">
          <div className="flex items-center justify-between desktop3:p-[1.5%] phone:px-[5%] desktop:px-[3%] desktop2:px-[3%] desktop:py-[3%] desktop2:py-[1%] laptop:px-[8%] laptop:py-[3%] phone:pb-[3%] tablet:px-[5%] tablet:py-[3%]">
            <p className="text-[28px] desktop:text-[24px] desktop2:text-[24px] font-bold phone:text-[18px] tablet:text-[18px] laptop:text-[18px]">Judul</p>
            <div onClick={closeModal} className="cursor-pointer ">
              <span class="material-symbols-outlined text-red-600 desktop3:text-[32px] phone:text-[26px] tablet:text-[26px] laptop:text-[26px] desktop:text-[26px]">close</span>
            </div>
          </div>
          <SlideImg image1={Rumah1} image2={Rumah1} />
        </Modal>
      </div>
    </Fade>
  )
}

export default Portofolio
