import React from 'react'
import Navbar from '../components/Navbar'
import Portofolio from '../components/Portofolio'
import Contacs from './Contacs'
import Footer from './Footer'

function Porto() {
  return (
    <div>
      <Navbar />
      <div className="desktop3:px-[11%] desktop:px-[7%] desktop2:px-[7%] phone:px-[5%] tablet:px-[5%] laptop:px-[8%] py-[1.5%] mt-[3%] ">
        <div className="flex items-center mb-[2rem]">
          <div className="bg-[#EAB200] mr-4 w-[0.2vw] h-[4vh] phone:w-[1vw] tablet:w-[1vw] laptop:w-[0.4vw] desktop:w-[0.4vw] desktop2:w-[0.4vw]"></div>
          <p className="text-[38px] desktop:text-[32px] desktop2:text-[32px]  phone:text-[24px] tablet:text-[24px] laptop:text-[24px] font-Quicksand font-bold text-[#031E33]">Portofolio</p>
        </div>
        <Portofolio />
      </div>

      <Contacs />
      <Footer />
    </div>
  )
}

export default Porto
